import { useState } from 'react'
import Project from './Project'

const Modal = ({projects , setPledgeAmount ,onShow , closeModal}) => {
    const [onConfirm , setConfirm] = useState(false)
    const [onFocus , setPledgetoGive] = useState(0)
    console.log(projects);
    const ConfirmModal = ({onShow}) =>{
        return(
            <div className={`${onConfirm ? 'modal-show' : 'modal-hide'} modal-conf modal-center`}>
                <div className="modal-confirm modal-container">
                    <div className="modal-header">
                        <img src="/images/icon-check.svg" alt="check" />
                    </div>
                    <div className="modal-content">
                        <h2>Thanks for your support!</h2>
                        <p>Your pledge brings us one step closer to sharing
                             Mastercraft Bamboo Monitor Riser worldwide. You will get
                                an email once our campaign is completed.</p>
                    </div>
                    <div className="modal-footer">
                        <button onClick={() => setConfirm(false)} className="btn-green">Got it!</button>
                    </div>
                </div>
            </div>
        )

    }

    const ProjectModal =() =>{
        return(
            <div className={`${onShow ? 'modal-show' : 'modal-hide'}  modal-bg modal-center`}>
            <div className="modal-container">
                <div className="modal-header">
                    <h2>Back this project</h2>
                    <p>Want to support us in bringing Mastercraft Bamboo Monitor Riser out in the world?</p>
                    <span><img onClick={closeModal} src="./images/icon-close-modal.svg" alt="close" /></span>
                </div>
                <div className="modal-content">   
                        <ProjectnoReward name={'Pledge with no reward'} 
                                        description={'Choose to support us without a reward if you simply believe in our project. As a backer, you will be signed up to receive product updates via email.'}
                                        quantity={'3'}/>
                    {projects.map((project) => (
                        <Project key={project.id} 
                                id={project.id}
                                name={project.name} 
                                quantity={project.quantity} 
                                price={project.price} 
                                description={project.description} />
                    ))}
                </div>
            </div>
            </div>
        )
    }

    const Project = ({id, name , quantity , price , description}) =>{
        return(
        <div>
        {quantity > 1 && <input type="radio" name="select"  className="project-checkbox" aria-label="radio"/> }
        <div className={`${quantity < 1 && 'modal-fade'} modal-project`}>
            <div className="modal-project--header">
           {quantity > 1  ? <h4 className="--mainText" onClick={(e) => onChecked(e.target.parentElement)}><span></span>{name}</h4>
                                : <h4>{name}</h4>
            }
            <span>Pledge ${price} or more</span>
                <div>
                    <h4>{quantity}</h4>
                    <small>left</small>
                </div>
            </div>
            <div className="modal-project--text">
                <p>{description}</p>
            </div>
            <div className="modal-quantity-sm">
                <h2>{quantity}</h2>
                <small>left</small>
            </div>
            <div className="modal-project--footer">
            <input type="text" aria-label="amount" placeholder="Enter your pledge"/>
            <div>
                <button onClick={() => sendAmount(price,id)} className="btn-white"><span>$</span>{price}</button>
                <button onClick={(e) => enterAmount(e,id)}  className="continue btn-green">Continue</button>
            </div>
            </div>
        </div>
        </div>
        )
    }

    const ProjectnoReward = ({name , description , quantity}) =>{
        return(
            <div>
            {quantity > 1 && <input type="radio" name="radio-button" name="select"  className="project-checkbox" aria-label="radio"/> }
            <div className={`${quantity < 1 && 'modal-fade'} modal-project`}>
                <div className="modal-project--header">
               {quantity > 1  ? <h4 className="--mainText" onClick={(e) => onChecked(e.target.parentElement)}><span></span>{name}</h4>
                                    : <h4>{name}</h4>
                }
                </div>
                <div className="modal-project--text">
                    <p>{description}</p>
                </div>
                <div className="modal-project--footer">
                <input type="text" id="email" aria-label="amount"  placeholder="Enter your Email"/>
                    <button onClick={() => sendAmount(0)} className="btn-green">Continue</button>

                </div>
            </div>
            </div>
        )
    }

    const onChecked = (e) =>{
        const checkbox = e.parentElement.previousSibling
        if(!checkbox) {
            return;
        }
        checkbox.checked = true

    }

   

    const enterAmount = (e,id) =>{
        const amount = e.target.parentElement.previousSibling.value
        sendAmount(amount,id)
    }

    const sendAmount = (amount,id) =>{
        setPledgeAmount(amount,id)
        setConfirm(!onConfirm)
        closeModal()
    }
    return (
        onConfirm ? <ConfirmModal /> :  <ProjectModal />

       
    )
}

export default Modal
