const Info = ({ pledge }) => {
    return (
        <section>
          <div className="container-info">
            <div>
              <h2>${pledge.amount.toLocaleString()}</h2>
              <p>of $100,000 backed</p>
            </div>
            <div>
                    <h2>{pledge.backers.toLocaleString()}</h2>
                    <p>total backers</p>
            </div>

            <div>
              <h2>56</h2>
              <p> days left</p>
            </div>
          </div>
          <div className="progress-bar">
            <div className="--bar"></div>
          </div>
        </section>
    )
}

export default Info
