import { useState } from 'react'
import Navbar from './components/NavBar'
import Header from './components/Header'
import Info from './components/Info'
import About from './components/About';
import Project from './components/Project';
import Modal from './components/Modal'
import './styles/App.css';

function App() {
  const [Bookmark , setBookmark] = useState(false)
  const [showModal, setShowModal] = useState(false)
  const [pledge , pledgeAmount] = useState({
    amount: 89914,
    backers: 5007
  })
  const[projects , setProjects] = useState([
    
{
  id : 1,
  name: "Bamboo Stand",
  price:25,
  description: "You get an ergonomic stand made of natural bamboo. You've helped us launch our promotional campaign, and you’ll be added to a special Backer member list.",
  quantity: 101
},
{
  id : 2,
  name: "Black Edition Stand",
  price: 75,
  description: "You get a Black Special Edition computer stand and a personal thank you. You’ll be added to our Backer member list. Shipping is included.",
  quantity: 64
},
{
  id : 3,
  name: "Mahogany Special Edition",
  price: 200,
  description: "You get two Special Edition Mahogany stands, a Backer T-Shirt, and a personal thank you. You’ll be added to our Backer member list. Shipping is included.",
  quantity: 0
}

  ])

  const setPledgeAmount = (amount,id) =>{
    if(!amount){
      return
    }
    const amountToPledge= parseInt(amount);
    pledgeAmount({...pledge, amount : pledge.amount + amountToPledge , backers : pledge.backers + 1});
    
    setProjects(projects.map((project) => project.id === id 
                ? {...project , quantity : project.quantity - 1 } : project))
    
                const percentage = ((pledge.amount + amountToPledge) / 100000) * 100

    const bar = document.querySelector('.--bar');
    bar.style.width= percentage.toPrecision() + '%'

  }

  const onSetBookmark = () =>{

    setBookmark(!Bookmark)
  }

  const onShowModal = () =>{
    setShowModal(!showModal)
  }
  return (
    <div className="App">
      <Modal onShow={showModal} closeModal={onShowModal} projects={projects} setPledgeAmount={setPledgeAmount} />
      <Navbar />
      <div className="container">
        <Header onSetBK={onSetBookmark} showBK={Bookmark} onShowModal={onShowModal} />

        <Info pledge={pledge} />
        <div className="about-bg">
          <About />
        <div className="projects-container">
          <Project projects={projects} onShowModal={onShowModal} />
        </div>
        </div>
      </div>
    </div>
  );
}

export default App;
